import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';


const useStyles = makeStyles((theme) => ({
  statsContainer:{
    borderTop: '1px solid rgba(0, 0, 0, 0.12)',
    borderBottom: '1px solid rgba(0, 0, 0, 0.12)',
    backgroundColor: '#fafafa !important',
    padding: theme.spacing(3, 0, 3),
  },
  statsTitle:{
    fontFamily: 'IBM Plex Sans, sans-serif',
    fontWeight: 700,
  },
  statsSubTitle:{
    fontFamily: 'Inter, sans-serif',
    fontSize:'1.3rem',
    letterSpacing: '0.2rem'

  }
}));

const HeroDivider = () => {
  const classes = useStyles();

  return(
    <Box className={classes.statsContainer}>

      <Container align="center" maxWidth="max" >
      <Container align="center" maxWidth="md" >
        <Grid container p={3} rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={3}>
            <Typography variant="h3" className={classes.statsTitle}>
              5000
            </ Typography>
            <Typography color="secondary" className={classes.statsSubTitle}>
              SHAPES
            </ Typography>
          </Grid>
          <Grid item xs={3}>
            <Typography variant="h3" className={classes.statsTitle}>
              10
            </ Typography>
            <Typography color="secondary" className={classes.statsSubTitle}>
              SECONDS
            </ Typography>
          </Grid>
          <Grid item xs={3}>
            <Typography variant="h3" className={classes.statsTitle}>
              60
            </ Typography>
            <Typography color="secondary" className={classes.statsSubTitle}>
              FPS
            </ Typography>
          </Grid>
          <Grid item xs={3}>
            <Typography variant="h3" className={classes.statsTitle}>
              FLUID
            </ Typography>
            <Typography color="secondary" className={classes.statsSubTitle}>
              LOOPS
            </ Typography>
          </Grid>
        </Grid>

</Container>
      </Container>
    </Box>
  );
}

export default HeroDivider;
