
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListSubheader from '@material-ui/core/ListSubheader';
import Avatar from '@material-ui/core/Avatar';

import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    backgroundColor: '#ffffff',
    
  },

  listHeader:{
    textAlign: 'center',
    fontSize: '1.8rem',
    color: '#000000',
    fontWeight: 600,
    fontFamily: 'Inter, sans-serif',
    padding:'15px 0px',

  },
  headerHero:{
    fontWeight: 800,
    fontSize:'6.8rem',
    fontFamily: 'IBM Plex Sans, sans-serif'
  }
}));

const HeroSection = () => {
  const classes = useStyles();

  return(
    <Box className={classes.root}>
      <Container maxWidth="md" >
      <Box m={2} p={3} pt={5}>
      <Typography gutterBottom
        align="center"
        color="text.primary"
        variant="h1"
         className={classes.headerHero}
        gutterBottom
      >
        <strong>JustShapes</strong>
      </Typography>
      </Box>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
           <List>
              <Paper variant="outlined" className="listPaper">
              <Typography className={classes.listHeader}>
                Generative Art
              </Typography>
              <Divider variant="fullWidth" />
                <ListItem className={classes.root}>
                  <ListItemIcon>
                      <StarIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="Brain pleasing generative art" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="10 second, 60 FPS seamless loops" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="Collectable sets and uniques" secondary="" />
                </ListItem>
              </Paper>
            </List>

          </Grid>
          <Grid item xs={12} md={6}>
             <List>
              <Paper variant="outlined" >
              <Typography className={classes.listHeader}>
                Forever Charity
              </Typography>
              <Divider variant="fullWidth" />
                <ListItem className={classes.root}>
                  <ListItemIcon>
                      <StarIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="Donate to underprivileged kids" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="$1,000 of toys a month forever" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="100% of royalties to charity" secondary="" />
                </ListItem>
              </Paper>
            </List>
          </Grid>
        </Grid>
        <br /><br /><br /><br />


        </Container>
    </Box>
  );
}

export default HeroSection;
