import React from "react";
import Sketch from "react-p5";
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

let x = 50;
let y = 50;
let divWidth;
let divWidthHalf;

function getHeight() {
    let divElement = document.querySelector(".boxy");
    let elemHeight = divElement.clientHeight;
    //alert("This: " + elemHeight);
    divElement.style.height = elemHeight + "px";
}
function getWidth() {
    let divElement = document.querySelector(".boxy");
    divWidth = divElement.clientWidth;
    //alert("This: " + divWidth);
    divElement.style.width = divWidth + "px";
    divWidthHalf = divWidth / 2;
}



const useStyles = makeStyles((theme) => ({
  container:{
    width:'100%',
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(0, 0, 0),
    border:'0px solid red',
    lineHeight:'0',
    maxHeight: '250px',
    overflow: 'hidden',
  },
  test:{
    backgroundColor: '#ffffff !important',
    margin: 'auto',
    width: '100%',
  },
}));


export default (props) => {
  //
  const classes = useStyles();

	const setup = (p5, canvasParentRef) => {
    getWidth();
		// use parent to render the canvas in this ref
		// (without that p5 will render the canvas outside of your component)
		p5.createCanvas(1200, 1200, p5.WEBGL).parent(canvasParentRef);
    p5.pixelDensity(1);
    p5.angleMode(p5.DEGREES);
    //p5.ortho(-500,500,500,-500,0.01,500);

	};

	const draw = (p5) => {
		p5.background(255);

    p5.translate(0,-350);
    //lights.
    p5.lights();
    p5.ambientLight(75);
    p5.noStroke();
    p5.fill(p5.color(234, 51, 35));
    p5.push();
      p5.rotateX(60.02);
      p5.rotateY(p5.frameCount * 0.21);
      p5.box(200);
    p5.pop();
    p5.push();
      //fill(color(24, 90, 188));
      p5.shininess(0);
      p5.ambientMaterial(p5.color(24, 90, 188));
      p5.translate(400,-50);
      p5.rotateX(-100);
      p5.rotateX(p5.frameCount * -0.21);
      p5.rotateY(60.02);
      p5.torus(100, 70,  96, 64);
    p5.pop();
    p5.push();
      //fill(color(24, 90, 188));
      p5.shininess(0);
      p5.ambientMaterial(p5.color(244, 187, 37));
      p5.translate(-400,-50);
      p5.rotateX(-100);
      p5.rotateX(p5.frameCount * -0.21);
      p5.rotateY(p5.frameCount * -0.21);
      p5.cylinder(100, 200,  96, 64);
    p5.pop();
		// NOTE: Do not use setState in the draw function or in functions that are executed
		// in the draw function...
		// please use normal variables or class properties for these purposes
		x++;
	};

	return (
    <Container alignItems="center" align="center" className={classes.container}>
      <div className="boxy" alignItems="center" >
        <Sketch align="center" alignItems="center" className={classes.test} setup={setup} draw={draw} />
      </div>
    </Container>
  );
};
